import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { FileRequest, FileResponse } from './file.model';
import { MockService } from '../services';

@Injectable({ providedIn: 'root' })
export class FileService extends MockService {
    constructor(
        private httpClient: HttpClient
    ) {
        super();
    }

    public uploadFiles(request: FileRequest): Observable<FileResponse> {
        return this.verify(this.httpClient.post<FileResponse>('v1/Files/', request), request.mockData);
    }
}

