import { ElementRef } from '@angular/core';
import { Observable } from 'rxjs';
import { ValidationService } from './validation.service';

export interface INestableComponent {
    callback(): Observable<any>;
    getErrors(): Observable<SummaryError[]>;
}

export interface SummaryError {
    element: ElementRef,
    messages: string[];
}

export abstract class ValidationAction {
    key: string;
    execute: (payload: any, value?: any, rowIndex?: number) => Observable<boolean>;
    errorMessage: (element?: any, rowIndex?: string) => string;
    id?: string;
    isValid?: boolean;
    constructor(overridenErrorMessage?: (element?: any, rowIndex?: string) => string
    ) {
        this.errorMessage = overridenErrorMessage;
    }
}

export class RequiredValidationAction extends ValidationAction {
    constructor(
        overridenErrorMessage?: (element?: any, rowIndex?: string) => string
    ) {
        super(overridenErrorMessage);
        this.key = ValidationConstant.Required;
    }
}

export class EmailValidationAction extends ValidationAction {
    constructor(overridenErrorMessage?: (element?: any, rowIndex?: string) => string
    ) {
        super(overridenErrorMessage);
        this.key = ValidationConstant.Email;
    }
}

export class PhoneValidationAction extends ValidationAction {
    constructor(overridenErrorMessage?: (element?: any, rowIndex?: string) => string
    ) {
        super(overridenErrorMessage);
        this.key = ValidationConstant.Phone;
    }
}

export class CustomValidationAction extends ValidationAction {
    constructor(
        execute: (payload: any, value?: any, rowIndex?: number) => Observable<boolean>,
        overridenErrorMessage?: (element?: any, rowIndex?: string) => string
    ) {
        super(overridenErrorMessage);
        this.execute = execute;
        this.key = ValidationConstant.Custom;
    }
}

export class ValidationOption {
    validationName: string;
    actions: ValidationAction[] = [];
    valueResolver: (payload: any, rowIndex?: number) => any | any[];
    displayText?: string;
    validationId?: string;
    payloadRef?: () => any;
    dynamic?: boolean;
    scope?: string;
    errorTargetId?: string;
    errorMessageClass?: string = ValidationConstant.ErrorMessageClass;
    errorElementClass?: string = ValidationConstant.ErrorElementClass;
    successElementClass?: string = ValidationConstant.SuccessElementClass;
    constructor(init?: Partial<ValidationOption>) {
        Object.assign(this, init);
    }
}

export class ClientValidator {
    formRef: ElementRef;
    options: ValidationOption[] = [];
    payloadRef?: () => any;
    scope?: string;
    relatedValidationProviders?: ValidationService[] = [];
    constructor(init?: Partial<ClientValidator>) {
        Object.assign(this, init);
    }
}

export class ValidationConstant {
    public static Required: string = 'Required';
    public static Email: string = 'Email';
    public static Phone: string = 'Phone';
    public static Custom: string = 'Custom';
    public static ErrorMessage: string = 'Khng hp l vui lng th li.';
    public static ErrorElementClass: string = 'ng-invalid';
    public static SuccessElementClass: string = 'ng-valid';
    public static ErrorMessageClass: string = 'text-danger';
    public static DefaultErrorStyles: string = 'padding:0;list-style:none;font-weight:400;font-size:13px; width: 100%';
    public static DefaultErrorClass: string = 'validation-error';
    public static AttributeName: string = 'validation-name';
    public static AttributeOptionName: string = 'dynamic-option';
    public static AttributeDynamicName: string = 'dynamic-validation';
    public static ErrorTargetId: string = 'error-target-id';
    public static DynamicErrorAttribute: string = 'dynamic-error-attribute';
    public static ERROR_ELEMENT_ID = 'error-element-id';
    public static ERROR_ITEM_ELEMENT_ID = 'error-item-element-id';
    public static VALIDATION_ID = 'validation-id';
    public static ARRAY_SEQUENCE_ID = 'array-sequence-id';
};
